import React from 'react'
import { connect } from 'react-redux'
import { Redirect } from 'react-router-dom'
import { reduxForm } from 'redux-form'
import { loginAPI } from '../../../redux/auth_Reducer'
import { createField, LogIN, PassWord } from '../../common/dopComponent/Textaria'
import { required } from '../../common/validate/ComponentValidate'



const LoginForm = (props) => {
    return (
        <form onSubmit={props.handleSubmit}>

            {createField('login', LogIN, [required], 'text')}
            {createField('password', PassWord, [required], 'password')}
            {createField('checkBox', "input", [], 'checkBox',  'click me pleese')}
           
            {
                props.error ? <div style={{color:'red'}}>{props.error}</div>: null
            }
            <button>Login</button>
        </form>
    )
}

let LoginReduxForm = reduxForm({form:"login"})(LoginForm)

const Login = (props) => {

    let onSubmit= (formData) => {
        props.loginAPI(
            formData.login,
            formData.password,
            formData.checkBox,
            )
    }
    if(props.auth ) {
        return <Redirect to="/profile"/>
    }
    
    return (
        <div>
            <LoginReduxForm onSubmit={onSubmit} />
        </div>
    )
}

let mapStateToProps = (state) => {
    return {
        auth:state.auth.isfetch
    }
}

export default connect(mapStateToProps, {loginAPI})(Login)